  outline: none;
}
.my-button--style-ROUNDED {
  font-size: 0;
}
.my-button--style-ROUNDED button {
  outline: none;
  border-style: solid;
  cursor: pointer;
  -webkit-transition: background-color 200ms ease-out;
  transition: background-color 200ms ease-out;
}
