  border-width: 1px;
  border-radius: 8px;
  font-weight: bold;
}
.my-button--style-ROUNDED.my-button--size-SMALL button:active {
  position: relative;
  top: 1px;
  left: 1px;
}
.my-button--style-ROUNDED.my-button--size-MEDIUM button {
  padding: 4px 16px;
  font-size: 14px;
  line-height: 22px;
  border-width: 1px;
  border-radius: 16px;
  font-weight: bold;
}
