  border-width: 2px;
  border-radius: 64px;
  font-weight: bold;
}
.my-button--style-ROUNDED.my-button--size-XLARGE button:active {
  position: relative;
  top: 2px;
  left: 2px;
}
.my-button--color-WHITE_BLACK button {
  border-color: black;
  background: white;
  color: black;
}
.my-button--color-WHITE_BLACK button:hover {
  background-color: #e6e6e6;
}
.my-button--color-WHITE_BLACK button:active {
